@import "shared";

#text-container {
	width: 60vw;
	height: 100vh;
	position: absolute;
	left: 0;
	top: 0;
	padding-left: 20vw;
	padding-right: 20vw;
	margin: 0;
	text-align: center;
}

#title-pic {
	width: 100%;
	margin: 0;
	margin-top: 5vh;
}

#intro-photo {
	position: fixed;
	width: 69.6vh;
	height: 90vh;
	margin: 0;
}

#photo-container {
	position: absolute;
	left: 50vw;
	padding-left: 5vw;
	padding-top: 5vh;
	top: 0;
}

.about-text {
	width: 100%;
	margin-bottom: 3vh;
	text-align: justify;
}

.by {
	font-size: 2.5vh;
	margin-bottom: .3vh;
	margin-top: 0;
}

#head {
	margin-top: 3vh;
	font-size: 5vh;
	margin-bottom: 3vh;
}

.date {
	margin-bottom: 2vh;
}

.by {
	margin-bottom: 3vh;
}

.disclaimer {
	margin-left: 2vw;
	margin-right: 2vw;
	font-size: 1.75vh;
	margin-bottom: 3vh;
}

#about-pic-container {
	width: 30vw;
	float: right;
	margin-left: 5vw;
}

#about-pic {
	width: 100%;
	align: right;
}

a {
	color: black;
	text-decoration: none;
}

a :hover {
	color: black;
	text-decoration: underline;
}

body {
	background-color: $default-background-color;
}
